function Buttons() {
    function sendMessage(agree:boolean) {
        if(!window.parent)return;
        window.parent.postMessage({sender:"privacy_page",agree}, '*');   
    }
    function onDisagree() {
        sendMessage(false);
    }
    function onAgree() {
        sendMessage(true);
    }
  return (
    <Grid2 container spacing={2} >
      <Grid2 size={6} sx={{ textAlign: 'center' }}>
        <Button onClick={onDisagree}>不同意</Button>
      </Grid2>
      <Grid2 size={6} sx={{ textAlign: 'center' }}>
        <Button onClick={onAgree} variant="contained">同意</Button>
      </Grid2>
    </Grid2>
  );
}

